.myworkContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
}

.myworkContainer .myworkBackground {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: rgb(15, 25, 36);
}

.myworkContainer .myworkBackground .myworkBack{
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #263272;
    position: absolute;
    left: 15px;
    top: 15px;
    border-radius: 5px;
    color: white;
    font-size: 30px;
    cursor: pointer;
}


.myworkContainer .myworkBackground .gameCardContainer{
    display: flex;
    justify-content:center;
    flex-wrap: wrap;
    align-items: center;
    width: 95%;
    height:95%;
    gap: 20px;
    overflow: scroll;
}

.myworkContainer .myworkBackground .gameCardContainer::-webkit-scrollbar{
    width: 10px;
}

.myworkContainer .myworkBackground .gameCardContainer::-webkit-scrollbar-track{
    background-color: rgb(15, 25, 36);
}

.myworkContainer .myworkBackground .gameCardContainer::-webkit-scrollbar-thumb{
    background-color: #082d5e;
    border-radius: 10px;
}

.myworkContainer .myworkBackground .gameCardContainer::-webkit-scrollbar-corner{
    background: none;
}

/*Mobile resolution*/
@media screen and (max-width: 450px) {
    .myworkContainer .myworkBackground .gameCardContainer{
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: start;
    }
}