.Transition{
    display: flex;
    position: absolute;
    width: 0.2%;
    background-color: black;
    height: 100vh;
    right: 0;
    z-index: 11;
    animation: slice-transition 2s;
    top: 0;
}

@keyframes slice-transition {
    0%{
        width: 0.2%;
    }
    25%{
        width: 100%;
    }
    80%{
        width:100%;
        left: 0;
    }
    100%{
        left: 0;
        width: 0%;
    }
}