.hero-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: white;
}

.hero-container .presentation{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}

.hero-container .presentation h2 {
    margin: 0;
}

.hero-container h2 {
    font-family: Verdana;
}

.hero-container .presentation h1 {
    margin: 0;
    font-size: 60px;
    font-family: Verdana;
}

.hero-container p {
    font-size: 50px;
    z-index: 10;
    font-family:fantasy;
}

.hero-container p:hover{
    cursor: pointer;
}

@media screen and (max-width: 1366px){
    .hero-container .presentation h1{
        font-size: 40px;
    }
    .hero-container .presentation h2{
        font-size: 20px;
    }
    .hero-container h2 {
        font-size: 20px;
        font-family: Verdana;
    }
    .hero-container p {
        font-size: 30px;
        z-index: 1;
        font-family:fantasy;
    }
}

@media screen and (max-width: 850px){
    .hero-container .presentation h1{
        font-size: 25px;
    }
    .hero-container .presentation h2{
        font-size: 18px;
    }
    .hero-container h2 {
        font-size: 18px;
        font-family: Verdana;
    }
    .hero-container p {
        font-size:22px;
        z-index: 10;
        font-family:fantasy;
    }
}

@media screen and (max-width: 370px){
    .hero-container .presentation h1{
        font-size: 22px;
    }
}


@media (prefers-reduced-motion: no-preference) {
    .hero-container p{
        animation: pressed-enter-fade infinite 1s;
    }
    .hero-container .letsgooo{
        animation: blink-letsgooo infinite 0.5s;
    }
}

@keyframes pressed-enter-fade {
    50%{
        opacity: 0;
    }
}

@keyframes blink-letsgooo {
    49%{
        opacity: 1;
    }
    50%{
        opacity: 0;
    }
    99%{
        opacity: 0;
    }
}