.trees-container{
    display: flex;
    position: relative;
    margin: 0 5vw 0 5vw;
}

.tree-base{
    width:25px;
    height:50px;
    background-color: rgb(17, 12, 0);
    z-index: 4;
}

.tree-base::before{
    content: '';
    width: 0;
    height: 0;
    border-left: 40px solid transparent;
    border-right: 40px solid transparent;
    border-bottom: 100px solid rgb(5, 39, 28);
    position: absolute;
    left: -25px;
    bottom: 40px;
    z-index: 4;
}

.tree-base::after{
    content: '';
    width: 0;
    height: 0;
    border-left: 40px solid transparent;
    border-right: 40px solid transparent;
    border-bottom: 100px solid rgb(5, 39, 28);
    position: absolute;
    left: -25px;
    bottom: 80px;
    z-index: 4;
}

@media screen and (max-width: 500px) {
    .tree-base{
        width: 12.5px;
        height: 25px;
    }

    .tree-base::before{
        border-left: 20px solid transparent;
        border-right: 20px solid transparent;
        border-bottom: 50px solid rgb(5, 39, 28);
        left: -12.5px;
        bottom: 20px;
    }

    .tree-base::after{
        border-left: 20px solid transparent;
        border-right: 20px solid transparent;
        border-bottom: 50px solid rgb(5, 39, 28);
        left: -12.5px;
        bottom: 40px;
    }
}

@media screen and (max-height: 667px){
    .tree-base{
        width: 12.5px;
        height: 25px;
    }

    .tree-base::before{
        border-left: 20px solid transparent;
        border-right: 20px solid transparent;
        border-bottom: 50px solid rgb(5, 39, 28);
        left: -12.5px;
        bottom: 20px;
    }

    .tree-base::after{
        border-left: 20px solid transparent;
        border-right: 20px solid transparent;
        border-bottom: 50px solid rgb(5, 39, 28);
        left: -12.5px;
        bottom: 40px;
    }
}
@media (prefers-reduced-motion: no-preference) {
    .trees-container{
        animation: trees-movement infinite 1s linear;
    }
}

