.gameCardBox{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 30%;
    height: 30%;
    cursor: pointer;
    box-shadow: 5px 5px 5px rgb(5, 5, 5);
}

.gameCardBox .gameCardImg{
    height: 75%;
    width: 100%;
    background-color: rgb(22, 22, 22);
}

.gameCardBox img {
    width: 100%;
    height: 100%;
}

.gameCardBox .gameCardText{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 25%;
    background-color: black;
}

.gameCardBox .gameCardText p {
    font-size: 20px;
    font-weight: 500;
    color: white;
}

@media screen and (max-width: 450px) {
    .gameCardBox{
        width: 80%;
    }
}