.select-container{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.select-background{
    width: 100%;
    height: 100%;
    z-index: -1;
    display: flex;
    justify-content: center;
    align-items: center;
}

.select-background .rustBox{
    display: flex;
    max-width: 1366px;
    max-height: 768px;
    position: absolute;
}

.select-box{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    position: absolute;
    height: 90%;
}

.select-background .darkBackground{
    width: 2560px;
    background-color: black;
    position: absolute;
    z-index: -2;
}

.select-background .rustBox img{
    width: 100%;
}

.select-container h1 {
    margin: 0;
    font-size: 80px;
    font-family: fantasy;
    letter-spacing: 10px;
    background: linear-gradient(#c0bcb6, #3f2404 50%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    -webkit-text-stroke: 1px black;
}

.options-border{
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 1300px;
    max-height: 485px;
    background: linear-gradient(#3a1d12, rgb(67, 36, 28), #381607);
    border-radius: 20px;
}

.options-container{
    transform: rotate(0);
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
    width: 97.5%;
    height: 92%;
}

.backButton{
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    width: 200px;
    height: 10%;
    background-color: gray;
    border-radius: 10px;
    top: 10px;
    left: 10px;
    cursor: pointer;
}

.backButton p {
    font-size: 16px;
    font-weight: bold;
}

.chestContainer{
    width: 70%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: row;
}

.plateContainer {
    width: 90%;
    height: 6%;
    display: flex;
    justify-content: space-around;
}

.plateContainer .plate:nth-of-type(1){
    transform: rotate(4deg);
}

.plateContainer .plate:nth-of-type(2){
    transform: rotate(-4deg);
}

.plateContainer .plate:nth-of-type(3){
    transform: rotate(4deg);
}

.plateContainer .plate:nth-of-type(4){
    transform: rotate(-4deg);
}

.options-container .options-border{
    position: absolute;
    border-radius: 40px 0 0 0;
    transform: translate(-22px, -22px);
    left: 0;
    top:0;
    width: 90px;
    height: 30px;
    background: linear-gradient(90deg, rgb(48, 22, 10),#612f0f 30%, rgb(48, 22, 10));
}

.options-container .options-border::before{
    content: '';
    position: absolute;
    border-radius: 40px 0 0 0;
    left: 0;
    top:0;
    width: 30px;
    height: 90px;
    background: linear-gradient(90deg, rgb(48, 22, 10), #612f0f 60%);
}

.options-container .options-border2{
    position: absolute;
    border-radius: 0 40px 0 0;
    transform: translate(22px, -25px);
    right: 0;
    top:0;
    width: 90px;
    height: 30px;
    background: linear-gradient(90deg, rgb(48, 22, 10),#612f0f 70%, rgb(48, 22, 10));
}

.options-container .options-border2::before{
    content: '';
    position: absolute;
    border-radius: 0 40px 0 0;
    right: 0;
    top:0;
    width: 30px;
    height: 90px;
    background: linear-gradient(90deg,#612f0f,rgb(48, 22, 10));
}

.options-container .options-border3{
    position: absolute;
    border-radius: 0 0 40px 0;
    transform: translate(40px, 40px);
    right: 0;
    bottom:0;
    width: 90px;
    height: 45px;
    background: linear-gradient(90deg, rgb(48, 22, 10),#612f0f 50%, rgb(48, 22, 10));
}

.options-container .options-border3::before{
    content: '';
    position: absolute;
    border-radius: 0 0 40px 0;
    right: 0;
    bottom:0;
    width: 45px;
    height: 90px;
    background: linear-gradient(90deg, #612f0f, rgb(48, 22, 10));
}

.options-container .options-border4{
    position: absolute;
    border-radius: 0 0 0 40px;
    transform: translate(-40px, 40px);
    left: 0;
    bottom:0;
    width: 90px;
    height: 45px;
    background: linear-gradient(90deg, rgb(48, 22, 10),#612f0f 50%, rgb(48, 22, 10));
}

.options-container .options-border4::before{
    content: '';
    position: absolute;
    border-radius: 0 0 0 40px;
    left: 0;
    bottom:0;
    width: 45px;
    height: 90px;
    background: linear-gradient(90deg, rgb(48, 22, 10),#612f0f);
}

/*Mobile design*/
@media screen and (max-width: 896px) {
    .select-background{
        position: relative;
    }

    .select-background .darkBackground{
        display: none;
    }

    .select-background .rustBox{
        height: 100%;
        max-height: max-content;
    }

    .select-background .rustBox img{
        height: 100%;
    }

    .select-container h1 {
        font-size: 30px;
        letter-spacing: 5px;
    }

    .options-border{
        max-width: 280px;
    }

    
    .options-container {
        flex-direction: column;
        width: 90%;
        overflow: scroll;
        border: none;
    }

    .options-container .options-border,
    .options-container .options-border2,
    .options-container .options-border3,
    .options-container .options-border4{
        display: none;
    }

    .backButton{
        width: 110px;
        height: 30px;
    }
    
    .backButton p{
        font-size: 10px;
    }

    .plate{
        display: none;
    }

    .select-box h1{
        font-family:Arial, Helvetica, sans-serif;
    }
}
