.optionContainer {
    height: 100%;
    width: 25%;
    background-color: rgb(17, 17, 17);
    border: 2px solid #0e0a08;
    cursor: pointer;
    display: flex;
    align-items: center;
    justify-content: center;
}

.optionContainer img{
    width: 100%;
    height: 100%;
    filter: grayscale(100%);
}

.optionContainer img:hover{
    filter: grayscale(0);
}

@media screen and (max-width: 896px){
    .optionContainer{
        width: 100%;
        border: none;
        border-bottom: 2px solid #0e0a08;
    }
}