.borealContainer{
    position: absolute;
    width: 100%;
    height: 100vh;
}

.boreal:nth-of-type(1) {
    position: absolute;
    width: 5%;
    height: 100%;
    bottom: 8%;
    filter:blur(40px);
    background: radial-gradient(#1a9b54 40%, transparent);
    opacity: 0.6;
    left: 100px;
    animation: grow 30s ease-in-out infinite;
}

.boreal:nth-of-type(2) {
    position: absolute;
    width: 5%;
    height: 100%;
    bottom: 8%;
    filter:blur(40px);
    background: radial-gradient(#a631b1 40%, transparent);
    opacity: 0.6;
    animation: grow 20s ease-in-out infinite;
}

.boreal:nth-of-type(3) {
    position: absolute;
    width: 5%;
    height: 100%;
    bottom: 8%;
    filter:blur(40px);
    background: radial-gradient(#1fa498 40%, transparent);
    opacity: 0.6;
    animation: grow 10s ease-in-out infinite;
}

@keyframes grow{
    0%{
        left:0px;
    }
    50%{
        width: 2%;
        opacity: 0.4;
        left: 95vw;
    }
    100%{
        left: 0%;
    }
}