.plate{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 16%;
    height: 100%;
    background-color: rgb(25,15,4);
    box-shadow: 10px 8px 20px black;
    border-radius: 10px;
}

.plate .platePoint:nth-of-type(1){
    position: absolute;
    left:2%;
    top: 5%;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    background-color: rgb(32, 22, 16);
}

.plate .platePoint:nth-of-type(2){
    position: absolute;
    right:2%;
    top: 5%;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    background-color: rgb(32, 22, 16);
}

.plate .platePoint:nth-of-type(3){
    position: absolute;
    right:2%;
    bottom: 5%;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    background-color: rgb(32, 22, 16);
}

.plate .platePoint:nth-of-type(4){
    position: absolute;
    left:2%;
    bottom: 5%;
    width: 10px;
    height: 10px;
    border-radius: 100%;
    background-color: rgb(32, 22, 16);
}

.plate .plateTitleBox{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 85%;
    height: 70%;
    background-color: rgb(73,69,49);
    border-radius: 10px;
}
.plate p{
    font-size: 20px;
    font-weight: 700;
    color: rgb(25,15,4);
}

@media screen and (max-width: 1400px){
    .plate{
        width: 14%;
        border-radius: 5px;
    }
    .plate .plateTitleBox{
        height: 70%;
        width: 80%;
        border-radius: 5px;
    }

    .plate p{
        font-size: 16px;
    }
}